export function nodeClass(type) {
	switch (type) {
		//dataflow 1-1
		case 'source':
		case 'sqlsource':
		case 'streaming_source':
		case 'source_dummy':
		case 'StartEvent':
			//case 'ConditionEvent':
			return 'classD_A';
		//dataflow 1
		case 'sink':
		case 'show':
			return 'classD_B';
		//dataflow -1-1
		case 'union':
		case 'intersect':
		case 'starjoin':
			return 'classD_C';
		//dataflow 12 -1
		case 'decision':
		case 'split':
		case 'validate':
			return 'classD_D';
		//dataflow 2 11
		case 'join':
		case 'productjoin':
		case 'minus':
			return 'classD_E';
		//workflow -1-1
		case 'dataflow':
		case 'hawq':
		case 'hive':
		case 'mapreduce':
		case 'shell':
		case 'spark':
		case 'event':
		case 'exclusive':
		case 'parallel':
			return 'classW_C';
		//other 11
		default:
			return 'classO';
	}
}

