var nodeIconFont = {
	source: 'source',
	sqlsource: 'sqlsource',
	sink: 'sink',
	decision: 'decision',
	validate: 'validate',
	supplement: 'supplement',
	sql: 'sql',
	transform: 'transform',
	filter: 'filter',
	sample: 'sample',
	lookup: 'lookup',
	join: 'join',
	starjoin: 'starjoin',
	productjoin: 'productjoin',
	aggregate: 'aggregate',
	top: 'top',
	union: 'union',
	intersect: 'intersect',
	minus: 'minus',
	split: 'split',
	Correlation: 'Correlation',
	Summary: 'Summary',
	gradientboogradientbostedtrees_predict: 'gradientboogradientbostedtrees_predict',
	MultilayerPerceptronPredict: 'MultilayerPerceptronPredict',
	MultilayerPerceptronTrain: 'MultilayerPerceptronTrain',
	kmeans_predict: 'kmeans_predict',
	kmeans_train: 'kmeans_train'
	// aggregate: "aggregate"
};
//
export function nodeIcon(type) {
	if (type in nodeIconFont) {
		return 'iconTrue';
	} else {
		return false;
	}
}

export function specialNodeClass(type) {
	switch (type) {
		//dataflow 1 2 yse -1 no -1
		case 'decision':
		case 'split':
			return 'classD_D1';
		//dataflow 1 2 ok -1 error -1
		case 'validate':
			return 'classD_D2';
		//dataflow 2 left 1 right 1 -1
		case 'join':
		case 'productjoin':
			return 'classD_E1';
		//dataflow 2 input1 1 input2 1 right 1 -1
		case 'minus':
			return 'classD_E2';
		//worflow  -1 -1
		case 'dataflow':
			return 'classW_A';
		// case "aggregate":
		//   return "classW_A";
	}
}

