export var connectorPaintStyle = {
	//
	strokeWidth: 2,
	stroke: '#4e5568',
	joinstyle: 'round',
	outlineColor: 'white',
	outlineWidth: 0
};
export var connectorHoverStyle = {
	// 
	strokeWidth: 3,
	stroke: '#ff4e4e',
	outlineColor: 'white',
	outlineWidth: 0
};
export var origin = {
	//
	endpoint: [ 'Dot', { radius: 8 } ], //
	connectorStyle: connectorPaintStyle, //
	connectorHoverStyle: connectorHoverStyle,
	paintStyle: {
		stroke: '#4e5568',
		fill: 'transparent',
		radius: 6,
		lineWidth: 6
	}, //
	//anchor: "AutoDefault",
	isSource: true, //
	connector: [ 'Flowchart', { stub: [ 5, 5 ], gap: 10, cornerRadius: 5, alwaysRespectStubs: true } ], //[Bezier],[Flowchart],[StateMachine ],[Straight ]
	isTarget: false, //
	maxConnections: 1, // ,-1
	connectorOverlays: [ [ 'Arrow', { width: 10, length: 10, location: 1 } ] ]
};
export var destination = {
	//
	endpoint: [ 'Dot', { radius: 6 } ], //
	connectorStyle: connectorPaintStyle, //
	connectorHoverStyle: connectorHoverStyle,
	paintStyle: { fill: '#4e5568' }, //
	isSource: false, //
	connector: [ 'Straight', { stub: [ 5, 5 ], gap: 10, cornerRadius: 5, alwaysRespectStubs: true } ], //[Bezier],[Flowchart],[StateMachine ],[Straight ]
	isTarget: true, //
	maxConnections: 1, // ,-1
	connectorOverlays: [ [ 'Arrow', { width: 10, length: 10, location: 1 } ] ]
};

export const addEndpointToNode = (jsplumbInstance, self, steps,flowType, flowData, _) => {
	jsplumbInstance.deleteEveryEndpoint();
	self.$nextTick(() => {
		steps.forEach((data, index) => {
			let drawType = data.type,
				dataIndex = data.id;

			//
			//
			if (nodeClass(drawType) == 'classD_A') {
				//jsplumbInstance.deleteEndpoint(dataIndex + "output" + "origin");
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'RightMiddle', maxConnections: 100 },
					{ uuid: dataIndex + 'output' + 'origin', ...origin }
				);
			} else if (nodeClass(drawType) == 'classD_B') {
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'LeftMiddle' },
					{ uuid: dataIndex + 'input' + 'destination', ...destination }
				);
			} else if (nodeClass(drawType) == 'classD_C' || nodeClass(drawType) == 'classW_C') {
				//()
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'LeftMiddle', maxConnections: -1 },
					{ uuid: dataIndex + 'input' + 'destination', ...destination }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'RightMiddle', maxConnections: -1 },
					{ uuid: dataIndex + 'output' + 'origin', ...origin }
				);
			} else if (specialNodeClass(drawType) == 'classD_D1') {

				if(flowType==="flink"){
					let anchors = addMultioutput(getOutputConfigurations(data.outputConfigurations, _));
					_.forEach(anchors, (val, index) => {
						jsplumbInstance.addEndpoint(
							dataIndex,
							{
								anchors: val.value,
								maxConnections: -1,
								overlays: [
									[
										'Label',
										{
											location: [ 3.5, 0 ],
											label: val.key,
											cssClass: 'endpointSourceLabelMult'
										}
									]
								]
							},
							{ uuid: dataIndex + val.key + 'origin', ...origin }
						);
					});
					//left
					jsplumbInstance.addEndpoint(
						dataIndex,
						{ anchors: 'LeftMiddle' },
						{ uuid: dataIndex + 'input' + 'destination', ...destination }
					);
				}else{
					jsplumbInstance.addEndpoint(
						dataIndex,
						{
							anchors: [ 1, 0.3, 0, 0 ],
							maxConnections: -1,
							overlays: [
								[
									'Label',
									{
										location: [ 1.5, -0.5 ],
										label: 'ok',
										cssClass: 'endpointSourceLabel'
									}
								]
							]
						},
						{ uuid: dataIndex + 'ok' + 'origin', ...origin }
					);
					jsplumbInstance.addEndpoint(
						dataIndex,
						{
							anchors: [ 1, 0.7, 0, 0 ],
							maxConnections: -1,
							overlays: [
								[
									'Label',
									{
										location: [ 1.5, 1.3 ],
										label: 'error',
										cssClass: 'endpointSourceLabel'
									}
								]
							]
						},
						{ uuid: dataIndex + 'error' + 'origin', ...origin }
					);
					jsplumbInstance.addEndpoint(
						dataIndex,
						{ anchors: 'LeftMiddle' },
						{ uuid: dataIndex + 'input' + 'destination', ...destination }
					);
				}
			
			} else if (specialNodeClass(drawType) == 'classD_D2') {
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 1, 0.3, 0, 0 ],
						maxConnections: -1,
						overlays: [
							[
								'Label',
								{
									location: [ 1.5, -0.5 ],
									label: 'ok',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'ok' + 'origin', ...origin }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 1, 0.7, 0, 0 ],
						maxConnections: -1,
						overlays: [
							[
								'Label',
								{
									location: [ 1.5, 1.3 ],
									label: 'error',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'error' + 'origin', ...origin }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'LeftMiddle' },
					{ uuid: dataIndex + 'input' + 'destination', ...destination }
				);
			} else if (specialNodeClass(drawType) == 'classD_E1') {
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'RightMiddle', maxConnections: -1 },
					{ uuid: dataIndex + 'output' + 'origin', ...origin }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 0, 0.3, 0, 0 ],
						overlays: [
							[
								'Label',
								{
									location: [ -1, -0.5 ],
									label: 'left',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'left' + 'destination', ...destination }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 0, 0.7, 0, 0 ],
						overlays: [
							[
								'Label',
								{
									location: [ -1, 1.5 ],
									label: 'right',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'right' + 'destination', ...destination }
				);
			} else if (specialNodeClass(drawType) == 'classD_E2') {
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'RightMiddle', maxConnections: -1 },
					{ uuid: dataIndex + 'output' + 'origin', ...origin }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 0, 0.3, 0, 0 ],
						overlays: [
							[
								'Label',
								{
									location: [ -1, -0.5 ],
									label: 'input1',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'input1' + 'destination', ...destination }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{
						anchors: [ 0, 0.7, 0, 0 ],
						overlays: [
							[
								'Label',
								{
									location: [ -1, 1.5 ],
									label: 'input2',
									cssClass: 'endpointSourceLabel'
								}
							]
						]
					},
					{ uuid: dataIndex + 'input2' + 'destination', ...destination }
				);
			} else {
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'RightMiddle', maxConnections: -1 },
					{ uuid: dataIndex + 'output' + 'origin', ...origin }
				);
				jsplumbInstance.addEndpoint(
					dataIndex,
					{ anchors: 'LeftMiddle', maxConnections: drawType == 'sql' ? -1 : 1 },
					{ uuid: dataIndex + 'input' + 'destination', ...destination }
				);
			}
			jsplumbInstance.draggable(dataIndex, {
				// containment: 'parent',
				start(params) {
					// 
					// console.log(params);
					//console.log("");
				},
				drag(params) {
					// 
				},
				stop(params) {
					let top = params.el.style.top;
					let left = params.el.style.left;
					// 
					// console.log("");
					flowData({
						x: parseInt(left.replace('px', '')),
						y: parseInt(top.replace('px', '')),
						id: params.el.attributes.id.nodeValue
					});
				}
			});
		});
	});
};

export const getOutputConfigurations = (val, _) => {
	let result = [];
	_.forEach(val, (value, key) => {
		result.push({ key: key, value: value });
	});
	return result;
};

//  val output 
export const addMultioutput = (list) => {
	// [1,20] 120
	// [1, 0, 0, 0]

	let val = list.length;

	let lineheight = 1;
	let y = 0;

	switch (val) {
		case 21:
		case 20:
		case 19:
		case 18:
		case 17:
			lineheight = 4;
			y = -1.33;
			break;
		case 16:
		case 15:
			lineheight = 4;
			y = -1.25;
			break;
		case 14:
		case 13:
		case 12:
			lineheight = 3;
			y = -0.73;
			break;

		case 11:
			// case 10:
			lineheight = 2.5;
			y = -0.65;
			break;

		case 10:
			lineheight = 2.5;
			y = -0.6;
			break;
		case 9:
		case 8:
			lineheight = 2;
			y = -0.45;
			break;

		case 7:
			lineheight = 1.8;
			y = -0.2;
			break;

		case 5:
			lineheight = 2;
			y = -0.33;
			break;
		case 6:
			lineheight = 2;
			y = -0.35;
			break;
		case 4:
		case 3:
			lineheight = 1;
			y = 0.15;
			break;
		case 2:
			lineheight = 1;
			y = 0.25;
			break;
		case 1:
			lineheight = 1;
			y = 0.5;
			break;
		default:
			'';
	}

	let distance = lineheight / val;
	let result = [];

	for (let j = 0; j < list.length; j++) {
		result.push({ key: list[j].key, value: [ 1, y, 0, 0 ] });
		y += distance;
	}

	return result;
};

export const connect = (jsplumbInstance, self, links, connectCallback) => {
	self.$nextTick(() => {
		//
		links.forEach((item) => {
			jsplumbInstance.connect({
				uuids: [ item.source + item.sourceOutput + 'origin', item.target + item.input + 'destination' ]
			});
		});
		connectCallback();
	});
};

export const getNodeType = (drawType) => {
	if (nodeClass(drawType) == 'classD_A') {
		return 'RightMiddle1';
	} else if (nodeClass(drawType) == 'classD_B') {
		return 'LeftMiddle1';
	} else if (nodeClass(drawType) == 'classD_C' || nodeClass(drawType) == 'classW_C') {
		return 'LeftMiddle1RightMiddle1';
	} else if (specialNodeClass(drawType) == 'classD_D1') {
		return 'LeftMiddle1yesno';
	} else if (specialNodeClass(drawType) == 'classD_D2') {
		return 'LeftMiddle1okerror';
	} else if (specialNodeClass(drawType) == 'classD_E1') {
		return 'leftrightRightMiddle';
	} else if (specialNodeClass(drawType) == 'classD_E2') {
		return 'input1input2RightMiddle';
	} else {
		return 'LeftMiddle1RightMiddle1';
	}
};

export const setClass = (type) => {
	let result = '';
	if (type == 'classD_A') {
		result = 't1Style';
	} else if (type == 'classD_B') {
		result = 't3Style';
	} else {
		result = 't2Style';
	}

	return result;
};

export const filterLinkData = (data, _) => {
	return _.map(data, (value) => {
		//sourceOutput
		if (value.sourceOutput) {
			value.sourceOutput = value.sourceOutput;
		} else {
			value.sourceOutput = 'output';
		}

		//targetInput

		if (nodeClass(value.targetInput) == 'classD_C') {
			value.targetInput = value.source;
		}

		if (value.input) {
			value.targetInput = value.input;
		} else {
			value.targetInput = 'input';
		}

		//input
		if (value.input) {
			value.input = value.input;
		} else {
			value.input = 'input';
		}

		return value;
	});
};

