export const message = (message, callback, self) => {
	self
		.$confirm(message, '', {
			confirmButtonText: '',
			cancelButtonText: '',
			type: 'warning'
		})
		.then(() => {
			self.$message({
				type: 'success',
				message: '!'
			});
			callback();
		})
		.catch(() => {
			self.$message({
				type: 'info',
				message: ''
			});
		});
};
